<template>
  <div class="widgetContainer userProfile">
    <div class="navigationHeader">
      <div class="navigationHeader__inner">
        <p class="title">
          {{ $t('personal_profile_header') }}
        </p>
        <span class="el-icon-close" @click="drawerClose" />
      </div>
    </div>
    <div class="widgetContainer__body personal-profile-body">
      <div class="listWithInfo border-1">
        <ul>
          <li>
            <p class="label">
              {{ $t('profile_name') }}
            </p>
            <p class="value">
              {{ getPersonDetails | contactName }}
            </p>
          </li>
          <li>
            <p class="label">
              {{ $t('profile_number') }}
            </p>
            <p class="value">
              {{ getPersonDetails.phone | phone }}
            </p>
          </li>
          <li>
            <p class="label">
              {{ $t('profile_email') }}
            </p>
            <p class="value">
              {{ getPersonDetails.email }}
            </p>
          </li>
        </ul>
      </div>

      <div class="listWithAccountNumbers" v-if="getBusinessList.length !== 0">
        <p class="header-2">
          {{ $t('profile_business_header') }}
        </p>
        <ul>
          <li v-for="b in getBusinessList" :key="b.id">
            <div class="details">
              <p class="label">
                {{ b.legalName }}
              </p>
            </div>
          </li>
        </ul>
      </div>

      <div>
        <p class="header-2">
          {{ $t('profile_action_header') }}
        </p>
        <div class="listWithActionsv2">
          <ul>
            <li
              @click="helpCenter()"
              v-if="isProfileHelpCenterEnabled">
              <div class="details">
                <p class="label">
                  {{ $t('profile_helpcenter') }}
                </p>
              </div>
              <div class="action">
                <span class="icon-Arrow-small-right icon" />
              </div>
            </li>
            <li
              @click="openHelp()"
              v-if="isProfileGetInTouchEnabled">
              <div class="details">
                <p class="label">
                  {{ $t('profile_getintouch') }}
                </p>
              </div>
              <div class="action">
                <span class="icon-Arrow-small-right icon" />
              </div>
            </li>
          </ul>
        </div>
      </div>

      <div class="addressWithInfo">
        <div class="addressWithInfo__header flex-between-center">
          <p class="header-2">
            {{ $t('location') }}
          </p>
        </div>

        <div class="addressWithInfo__body border-1">
          <div class="map">
            <ul class="card-with-map">
              <div class="map">
                <GmapMap
                  v-if="lat && lng"
                  :center="{ lat, lng }"
                  :zoom="7"
                  map-type-id="terrain"
                  style="width: 100%; height: 200px">
                  <GmapMarker
                    :position="{ lat, lng }"
                    :clickable="true"
                    :draggable="false" />
                </GmapMap>
              </div>

              <ul>
                <li>
                  <span class="label">{{ $t('address') }}</span>
                  <span class="value">{{ address }}</span>
                </li>
                <li>
                  <span class="label">{{ $t('contact_country_title') }}</span>
                  <span class="value">
                    {{ getPersonDetails.address.country }}
                  </span>
                </li>
              </ul>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <div class="el-drawer__footer">
      <el-button type="primary" @click="logoutHandler" class="logout brad width-100 el-button__brand brand">
        {{ $t('profile_logoutn_btn') }}
      </el-button>
    </div>
  </div>
</template>

<script>
import { gmapApi } from 'vue2-google-maps';
import { mapGetters } from 'vuex';
import loginMixins from '@m/user/mixins/login';
import { getBrandConfig, getUIConfig } from '@/utils/env';
export default {
  mixins: [loginMixins],
  data() {
    return {
      lat: null,
      lng: null,
      isProfileHelpCenterEnabled: getUIConfig('isProfileHelpCenterEnabled'),
      isProfileGetInTouchEnabled: getUIConfig('isProfileGetInTouchEnabled'),
      isProfileDisclosureEnabled: getUIConfig('isProfileDisclosureEnabled')
    };
  },
  created() {
    this.getLatLong();
  },
  computed: {
    ...mapGetters('user', ['getPersonDetails']),
    ...mapGetters('kyb', ['getBusinessList']),
    google: gmapApi,
    address() {
      const user = this.getPersonDetails;
      const address = user.address;
      if (address.line2 && address.line2.length > 0) {
        return (
          address.line1 +
          ', ' +
          address.line2 +
          ', ' +
          address.city +
          ', ' +
          address.state
        );
      }
      return address.line1 + ', ' + address.city + ', ' + address.state;
    }
  },
  methods: {
    done() {
      this.goToDashboard();
    },
    popupClose() {
      this.editAddressDialog = false;
    },
    getLatLong() {
      this.$gmapApiPromiseLazy().then(() => {
        if (this.google) {
          /** @type {import('@m/user/store/state').userState['personDetails'] } */
          const user = this.getPersonDetails;
          const geocoder = new this.google.maps.Geocoder();
          const address = user.address;
          const addressString =
            address.line1 +
            ',' +
            address.line2 +
            ',' +
            address.city +
            ',' +
            address.country;

          geocoder.geocode(
            {
              address: addressString
            },
            (results, status) => {
              if (status == this.google.maps.GeocoderStatus.OK) {
                const latitude = results[0].geometry.location.lat();
                const longitude = results[0].geometry.location.lng();
                this.lat = latitude;
                this.lng = longitude;
              }
            }
          );
        }
      });
    },
    logoutHandler() {
      this.logoutUser();
    },
    helpCenter() {
      var win = window.open(
        getBrandConfig('helpCenter','https://intercom.help/solid-2cf7e39d14f4/en/'),
        '_blank'
      );
      win.focus();
    },
  }
};
</script>

